import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import axios from 'axios';
import Basket from '../Basket/Basket';

const styles = theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing.unit * 2,
      margin: 'auto',
      maxWidth: 800,
    },
    image: {
      width: 128,
      height: 128,
    },
    img: {
      margin: 'auto',
      display: 'block',
      maxWidth: '100%',
      maxHeight: '100%',
    },
    searchBar: {
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center',
    },
    searchTextField: {
        width: "100%",
        marginRight: 20
    },
    buttonB: {
        width: 150,
        height: 50,
        marginRight: 20
    }
});

const pharseStyles = theme => ({
    name: {
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center'
    },
    typo: {
        width: "100%",
        margin: 20
    }
});

const PharsePiece = (props) => {
    const {classes} = props;
    return (
        <div className={classes.name} key={props.data.id}>
            <Typography className={classes.typo} variant="body1" gutterBottom align="left">
                {props.data.name}
            </Typography>
            <Button variant="contained" color="secondary" className={classes.button} onClick={props.function}>
                -
            </Button>
        </div>);
}

  const Pharse = withStyles(pharseStyles)(PharsePiece)

  class Phrases extends React.Component {
    state = {
        query: "",
        data: [],
        basket: null,
        algorithm: null,
        labelWidth: 100,
        parcelNumber: 1
    };
    
    handleChange = name => event => {
        this.setState({
            [name]: event.target.value,
        });
    };

    sendQuery = () => {
        axios("http://localhost:8080/api/cart", {
            method: "post",
            params: {
                phrase: this.state.query
            },
            withCredentials: true
        }).then((response) => {
            console.log(response)
            this.getBasket()
        }).catch(error => {
            console.log(error)
        })
    }

    getBasket = () => {
        axios("http://localhost:8080/api/cart", {
            method: "get",
            withCredentials: true
        }).then((response) => {
            console.log(response)
            this.setState({
                data: response.data
            })
        }).catch(error => {
            console.log(error)
        })
    }

    deleteItem = (item) => {
        axios("http://localhost:8080/api/cart", {
            method: "delete",
            data: item,
            withCredentials: true
        }).then((response) => {
            console.log(response)
            this.getBasket()
        }).catch(error => {
            console.log(error)
        })
    }

    calculateBasket = () => {
        let {algorithm, parcelNumber} = this.state
        if (algorithm == null) {
            algorithm="jaccard"
        }
        axios("http://localhost:8080/api/cart/proposes", {
            method: "get",
            params: {
                algorithm: algorithm,
                maxPacket: parcelNumber
            },
            withCredentials: true
        }).then((response) => {
            console.log(response)
            this.setState({basket: response.data.data})
        }).catch(error => {
            console.log(error)
        })
    }

    componentDidMount() {
        this.getBasket();
    }


    render() {
        const { classes } = this.props;

        const phrases = this.state.data.map((e) => (
            <Pharse data={e} function={() => this.deleteItem(e)}/>
        ))

        let basket = null

        if (this.state.basket != null) {
            basket = <Basket data={this.state.basket}/>
        }

        return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <div className={classes.searchBar}>
                    <TextField
                        id="outlined-name"
                        label="Wyszukaj..."
                        className={classes.searchTextField}
                        value={this.state.query}
                        onChange={this.handleChange('query')}
                        margin="normal"
                        variant="outlined"
                    />
                    <Button variant="contained" color="primary" className={classes.button} onClick={this.sendQuery}>
                        Dodaj
                    </Button>
                </div>
                <div>
                    {phrases}
                </div>
                <div>
                    <TextField
                        id="standard-number"
                        label="Liczba przesyłek"
                        value={this.state.parcelNumber}
                        onChange={this.handleChange('parcelNumber')}
                        type="number"
                        className={classes.textField}
                        InputLabelProps={{
                            shrink: true,
                        }}
                        margin="normal"
                    />
                    <FormControl component="fieldset">
                        <FormLabel component="algorithm">Algorytm</FormLabel>
                        <RadioGroup
                            aria-label="algorithm"
                            name="algorithm"
                            value={this.state.algorithm}

                            onChange={this.handleChange}
                            row
                        >
                            <FormControlLabel
                                value="jaccard"
                                control={<Radio color="primary" />}
                                label="Jaccard"
                                labelPlacement="bottom"
                            />
                            <FormControlLabel
                                value="cosinus"
                                control={<Radio color="primary" />}
                                label="Cosinus"
                                labelPlacement="bottom"
                            />
                        </RadioGroup>
                    </FormControl>
                    <Button variant="contained" color="primary" className={classes.buttonB} onClick={this.calculateBasket}>
                        Generuj
                    </Button>
                </div>
            </Paper>
            {basket}
        </div>
        );
    }
}
  
  export default withStyles(styles)(Phrases);