import React from "react";
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

const cardStyles = theme => ({
    card: {
      display: 'flex',
      width: "100%",
      marginBottom: 20
    },
    details: {
      display: 'flex',
      flexDirection: 'column',
      width: "100%"
    },
    content: {
      flex: '1 0 auto',
      width: "100%"
    },
    cover: {
      width: 151,
    },
    controls: {
      display: 'flex',
      alignItems: 'center',
      paddingLeft: theme.spacing.unit,
      paddingBottom: theme.spacing.unit,
    },
    playIcon: {
      height: 38,
      width: 38,
    },
  });

const styles = theme => ({
    paper: {
        padding: theme.spacing.unit * 2,
        margin: 'auto',
        maxWidth: 800,
      },
      endOfBasket: {
          border: "4px solid black"
      },
      endOfItem: {
          border: "2px dashed blue"
      }
})
  

const BasketItemPiece = (props) => {

    const {classes} = props

    return (
        <Card className={classes.card}>
            <div className={classes.details}>
                <CardContent className={classes.content}>
                    <Typography component="h5" variant="h5">
                        <a href={"https://allegro.pl/oferta/" + props.data.id}>{props.data.name}</a>
                    </Typography>
                    <Typography variant="subtitle1" color="textSecondary">
                        ID sprzedawcy: {props.data.seller.id}
                    </Typography>
                    <Typography variant="subtitle1" color="textSecondary">
                        Cena: {props.data.sellingMode.price.amount}
                    </Typography>
                </CardContent>
            </div>
            {/* <CardMedia
                className={classes.cover}
                image="/static/images/cards/live-from-space.jpg"
                title="Live from space album cover"
            /> */}
        </Card>
    )
}

const BasketItem = withStyles(cardStyles)(BasketItemPiece)

const Basket = (props) => {

    const {classes} = props

    const item = []
    let total = 0
    // console.log(props)
    for(let j = 0; j < props.data.length; ++j){
        item.push(<p style={{fontSize: "2em"}}>Koszyk</p>)
        // console.log(props.data[j])
        for (let index = 0; index < props.data[j].parcels.length; ++index) {
            const items = props.data[j].parcels[index].items;
            total += props.data[j].parcels[index].parcel.price;
            for (let i = 0, len = items.length; i < len; ++i) {
                item.push((<BasketItem data={items[i]}/>));
                console.log(items[i]);
                total += items[i].sellingMode.price.amount;
            }
            item.push(<hr className={classes.endOfItem}/>);
        }
        item.push(<p>Suma: {total.toFixed(2)}</p>);
        total = 0;
        item.push(<hr className={classes.endOfBasket}/>);
    }
    

    return (
    <Paper className={classes.paper}>
        {item}
    </Paper>);
}

export default withStyles(styles)(Basket);